import React from "react";

const QuranMp3 = ({ suraName , reader }) => {

  return (
    <div style={{width: "100%"}}>
      <audio style={{display: "block", margin: "0 auto", maxWidth: "20rem" , width: "100%"}} controls autoPlay src={`http://${reader}/${suraName}.mp3`}>

        Your browser does not support the audio element.
      </audio>
    </div>
  );
};


export default QuranMp3;
