import React  from "react";
import styled from "styled-components";


const Surra = ({suraName , setSuraName}) => {
  
  const Data = ["..السورة","الفَاتِحَة","البَقَرَة","آل عِمرَان","النِّسَاء","المَائدة","الأنعَام","الأعرَاف","الأنفَال","التوبَة","يُونس","هُود","يُوسُف","الرَّعْد","إبراهِيم","الحِجْر","النَّحْل","الإسْرَاء","الكهْف","مَريَم","طه","الأنبيَاء","الحَج","المُؤمنون","النُّور","الفُرْقان","الشُّعَرَاء","النَّمْل","القَصَص","العَنكبوت","الرُّوم","لقمَان","السَّجدَة","الأحزَاب","سَبَأ","فَاطِر","يس","الصَّافات","ص","الزُّمَر","غَافِر","فُصِّلَتْ","الشُّورَى","الزُّخْرُف","الدُّخان","الجاثِية","الأحقاف","مُحَمّد","الفَتْح","الحُجُرات","ق","الذَّاريَات","الطُّور","النَّجْم","القَمَر","الرَّحمن","الواقِعَة","الحَديد","المُجادَلة","الحَشْر","المُمتَحَنة","الصَّف","الجُّمُعة","المُنافِقُون","التَّغابُن","الطَّلاق","التَّحْريم","المُلْك","القَلـََم","الحَاقّـَة","المَعارِج","نُوح","الجِنّ","المُزَّمّـِل","المُدَّثــِّر","القِيامَة","الإنسان","المُرسَلات","النـَّبأ","النـّازِعات","عَبَس","التـَّكْوير","الإنفِطار","المُطـَفِّفين","الإنشِقاق","البُروج","الطّارق","الأعلی","الغاشِيَة","الفَجْر","البَـلـَد","الشــَّمْس","اللـَّيل","الضُّحی","الشَّرْح","التـِّين","العَلـَق","القـَدر","البَيِّنَة","الزلزَلة","العَادِيات","القارِعَة","التَكاثـُر","العَصْر","الهُمَزَة","الفِيل","قـُرَيْش","المَاعُون","الكَوْثَر","الكَافِرُون","النـَّصر","المَسَد","الإخْلَاص","الفَلَق","النَّاس"];

  const Numers = (num) => {
    if (num < 10)
      return `00${num}`;
    else if (num < 100)
      return `0${num}`;
    else
     return num;
  }

  const handelChange = (e) => {

    setSuraName(e.target.value);
  }
  return (
    <div>
      <SelectStyled onChange={handelChange}>
        {Data.map((sura , index) => (
          <option key={index} value={Numers(index)}>{sura}</option>
        ))}
      </SelectStyled>
    </div>
  );
};

const SelectStyled = styled.select`
    padding: .6rem;
    font-family: cairo;
    font-size: 1rem;
    font-weight: bold;
    border-radius: .3rem;
    border: none;
`;

export default Surra;
